@mixin notebook {
  @media (max-width: #{$desktop-width}) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: 800px) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: 1124px) {
    @content;
  }
}

@mixin desktop {
  @media screen and (min-width: 800px) {
    @content;
  }
}
